<template>
  <swiper class="play_page"
          vertical
          circular
          duration="200"
          @change="onChange"
          :current="getPlayPageIndex">
    <swiper-item class="item"
                 :class="[(!getIsPlay && getPlayPageIndex === index) && 'mask iconfont icon-audioPlay']"
                 @tap="onMask(index)"
                 v-for="(item, index) in renderQueue"
                 :key="item.id">
      <prepare-img :fz="22"></prepare-img>
      <image :src="item.picUrl + '?param=400y400'"
             class="img">
      </image>
    </swiper-item>
  </swiper>
</template>

<script>
import {BASE_URL} from "@/util";
import {mapState, mapMutations} from "vuex";
import types from "@/store/mutations-types";

export default {
  data() {
    return {

    }
  },
  computed: {
    ...mapState({
      getIsPlay: 'isPlay',
      getAudio: 'audio',
      getCurrentSong: 'currentSong',
      getPlayPageIndex: 'playPageIndex',
      getCurrentPlayQueue: 'currentPlayQueue',
    }),

    // 动态加载播放页面
    renderQueue() {
      let index = this.getPlayPageIndex, queue = this.getCurrentPlayQueue;
      if (index !== queue.length && index + 2 < queue.length) {
        return queue.slice(0, index + 2);
      }
      return queue;
    }
  },
  methods: {
    ...mapMutations([
        types.SET_PLAY_PAGE_INDEX
    ]),

    onMask(index) {
      let i = this.getPlayPageIndex;
      if (this.getIsPlay) {
        this[types.SET_PLAY_PAGE_INDEX](index);
        this.getAudio.pause()
      } else{
        this[types.SET_PLAY_PAGE_INDEX](index);
        this.getAudio.play()
      }
    },

    onChange(e) {
      let current = e.detail.current;
      this.renderQueue;
      this[types.SET_PLAY_PAGE_INDEX](current);
      this.$store.dispatch('getPlaySong', this.getCurrentPlayQueue[current]);
    },

    /*onImgLoad(e) {
      console.log(e);
    },

    onImgError(e) {
      console.log(e);
    }*/
  }
}
</script>

<style scoped lang="scss">
  .play_page{
    @include wh(100%, 100%);
    position: relative;
    .item{
      @include wh(100%, 100%);
      .img{
        @include wh(100%, 100%);
        will-change: transform;
      }
    }
  }
  .mask::after{
    content: '';
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0,0,0,.3);
    z-index: 999;
  }
  .mask::before{
    content: "\e734";
    color: $font-color-white;
    position: absolute;
    top: 50%;
    left: 50%;
    z-index: 1000;
    transform: translate3d(-50%, -50%, 0);
    font-size: 100rpx;
  }
</style>